body {
    background: #333;
}

.box-profile {
    display: flex;
    justify-content: center;
    align-items: center;
}

.arrow {
    margin: 5px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 20px;
    color: #FFF;
    transition: 0.5s;
    background: #1b1b1b;
    height: 100px;
    line-height: 100px;
    vertical-align: middle;
}

.arrow:hover {
    font-size: 25px;
    cursor: pointer;
}
